export default {
  name: "modalTest",
  data() {
    return {
      index: 0,
      showOption: false
    };
  },
  methods: {
    ClickModal(type, way, isAsync, status) {
      let that = this;
      if (way == "promise") {
        this.$modal({
          content: "我是通过promise调用的",
          showCancelButton: type == "confirm",
          confirmText: "点我",
          cancelText: "关闭"
        })
          .then(rlt => {
            console.log("确认点我");
          })
          .catch(rlt => {
            console.log("取消");
          });
      } else {
        this.$modal({
          title: "注意",
          content: "我是宇宙无敌爆炸帅",
          showCancelButton: type == "confirm",
          asyncClose: !!isAsync,
          confirm() {
            if (!!isAsync) {
              setTimeout(() => {
                if (status == "clear") {
                  that.$modal.clearLoading();
                } else {
                  that.$modal.close();
                  that.$toast("确认按钮(confirm异步)");
                }
              }, 1000);
            } else {
              that.$toast(
                type == "alert" ? "确认按钮(alert)" : "确认按钮(confirm)"
              );
            }
          },
          cancel() {
            console.log("取消按钮");
          }
        });
      }
    },
    ClickModalCustom() {
      this.$modal({
        title: "提示",
        content: `<img src=${require("../assets/img/cqObject-icon.png")} alt=''>`, // 传入html片段
        showCancelButton: true,
        confirm() {
          that.$toast("确认按钮");
        },
        cancel() {
          console.log("取消按钮");
        }
      });
    }
  },
  mounted() {}
};



